/\r\n  }\r\n\r\n  /* Style the scrollbar thumb */\r\n  ::-webkit-scrollbar-thumb {\r\n    background-color: rgb(162, 158, 159); /* Color of the scrollbar thumb */\r\n    border-radius: 5px; /* Rounded corners for the thumb */\r\n    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2); /* Inner shadow effect */\r\n  }\r\n\r\n  /* Style the scrollbar track */\r\n  ::-webkit-scrollbar-track {\r\n    background-color: #f1f1f1; /* Color of the scrollbar track */\r\n  }\r\n\r\n  scrollbar-width: thin;\r\n  scrollbar-color: rgb(162, 158, 159) #f1f1f1; // Firefox\r\n\r\n  &.chat-only {\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    max-width: 100%;\r\n    border-radius: 0;\r\n    .chat-header {\r\n      display: none;\r\n    }\r\n    .chatbox {\r\n      .chat-footer {\r\n        padding: 0;\r\n        border-top: none;\r\n      }\r\n    }\r\n  }\r\n  h1 {\r\n    font-size: 3.2em;\r\n    line-height: 1.1;\r\n  }\r\n  a {\r\n    font-weight: 500;\r\n    color: #646cff;\r\n    text-decoration: inherit;\r\n  }\r\n  a:hover {\r\n    color: #535bf2;\r\n  }\r\n\r\n  position: fixed;\r\n  bottom: 6rem;\r\n  right: 3rem;\r\n  width: 450px;\r\n  max-width: calc(100% - 3rem);\r\n  height: 85vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n  box-shadow: rgba(150, 150, 150, 0.2) 0px 10px 30px 0px,\r\n    rgba(150, 150, 150, 0.2) 0px 0px 0px 1px;\r\n  border-radius: 1rem;\r\n  overflow: hidden;\r\n  background: #fff;\r\n  @media screen and (max-width: 540px) {\r\n    right: 1rem;\r\n    max-width: calc(100% - 2rem);\r\n  }\r\n  .chatbox {\r\n    display: flex;\r\n    flex-direction: column;\r\n    height: 100%;\r\n    .chat-header {\r\n      flex: 0 0 auto;\r\n      border-bottom: 1px solid #ddd;\r\n      .chat-brand {\r\n        display: flex;\r\n        justify-content: space-between;\r\n        padding: 1rem;\r\n        .chatbox-logo {\r\n          width: auto;\r\n          object-fit: contain;\r\n          height: 36px;\r\n        }\r\n        .btn-wrapper {\r\n          display: flex;\r\n          justify-content: space-between;\r\n          gap: 0.5rem;\r\n          .close-icon {\r\n            display: flex;\r\n            align-items: center;\r\n            border: none;\r\n            outline: none;\r\n            background: transparent;\r\n            cursor: pointer;\r\n            img {\r\n              width: 16px;\r\n              cursor: pointer;\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n    .chat-body {\r\n      flex: 1 1 auto;\r\n      overflow-y: auto;\r\n      padding: 2rem 1rem;\r\n      // scrollbar-gutter: stable;\r\n      position: relative;\r\n\r\n      .chat-message {\r\n        display: flex;\r\n        flex-direction: column;\r\n        align-items: flex-start;\r\n        margin-bottom: 1rem;\r\n        .chat-content {\r\n          padding: 0.5rem 1rem;\r\n          border-radius: 0.5rem;\r\n          background-color: #f1f1f0;\r\n          max-width: 80%;\r\n          word-break: break-word;\r\n          .timestamp {\r\n            font-size: 0.75rem;\r\n            color: #999;\r\n            margin-top: 0.25rem;\r\n          }\r\n        }\r\n        &.user {\r\n          align-items: flex-end;\r\n          .chat-content {\r\n            background: #45c98d;\r\n            color: #fff;\r\n            .timestamp {\r\n              color: #fff;\r\n            }\r\n          }\r\n        }\r\n        img {\r\n          max-width: 100%;\r\n          width: 100%;\r\n          margin: 12px auto;\r\n          display: block;\r\n        }\r\n      }\r\n    }\r\n    .chat-footer {\r\n      display: flex;\r\n      flex: 0 0 auto;\r\n      padding: 1rem;\r\n      justify-content: space-between;\r\n      border-top: 1px solid #ddd;\r\n      .chat-input {\r\n        flex: 1 1 auto;\r\n        display: flex;\r\n        align-items: end;\r\n        justify-content: space-between;\r\n        border-radius: 0.5rem;\r\n        background-color: transparent;\r\n        padding: 0.5rem;\r\n        padding-right: 0;\r\n        border: 1px solid #ddd;\r\n        position: relative;\r\n        max-width: 100%;\r\n        textarea {\r\n          flex: 1 1 auto;\r\n          border: none;\r\n          outline: none;\r\n          padding-right: 3.5rem;\r\n          background-color: transparent;\r\n          font-size: 1rem;\r\n          color: #333;\r\n          resize: none;\r\n          font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;\r\n          &::placeholder {\r\n            font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;\r\n          }\r\n          scrollbar-width: thin;\r\n          scrollbar-color: #aaa transparent;\r\n        }\r\n        .chat-send,\r\n        .chat-stop {\r\n          margin-left: 1rem;\r\n          border: none;\r\n          outline: none;\r\n          background-color: transparent;\r\n          background: #45c98d;\r\n          cursor: pointer;\r\n          font-weight: bold;\r\n          color: #fff;\r\n          border-radius: 4px;\r\n          height: 100%;\r\n          display: flex;\r\n          justify-content: center;\r\n          align-items: center;\r\n          padding: 8px;\r\n          width: 40px;\r\n          height: 32px;\r\n          position: absolute;\r\n          right: 8px;\r\n          bottom: 50%;\r\n          transform: translateY(50%);\r\n          min-height: 24px;\r\n          &:disabled {\r\n            background: #d9d9d9;\r\n            cursor: not-allowed;\r\n          }\r\n          img {\r\n            width: 75%;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .collapsibleWrapper {\r\n    .collapsibleButton {\r\n      background-color: #555;\r\n      color: white;\r\n      cursor: pointer;\r\n      width: 100%;\r\n      text-align: left;\r\n    }\r\n\r\n    .collapsibleContent {\r\n      max-height: 0;\r\n      overflow: hidden;\r\n      transition: max-height 0.2s ease-out;\r\n    }\r\n\r\n    .button:active + .collapsibleContent {\r\n      max-height: 100vh;\r\n    }\r\n  }\r\n\r\n  .accordion .accordion-item.active .panel {\r\n    overflow-y: auto;\r\n  }\r\n\r\n  .accordion .accordion-item .title {\r\n    padding: 12px;\r\n  }\r\n}\r\n.enlarge-box-wrapper {\r\n  left: 3rem;\r\n  width: unset;\r\n  transition: left 0.3s ease-out, width 0.3s ease-out;\r\n}\r\n\r\n.chatbot-toggler {\r\n  position: fixed;\r\n  z-index: 99999999;\r\n  bottom: 1rem;\r\n  right: 3rem;\r\n  width: 50px;\r\n  height: 50px;\r\n  border-radius: 50%;\r\n  background: #45c98d;\r\n  color: #fff;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);\r\n  cursor: pointer;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  border: none;\r\n  font-weight: bold;\r\n  padding: 0;\r\n  @media screen and (max-width: 540px) {\r\n    right: 1rem;\r\n  }\r\n  img {\r\n    width: 24px;\r\n  }\r\n  &.toggler-spinner {\r\n    background: #fff;\r\n  }\r\n}\r\n.regenerate-button {\r\n  background-color: #fff;\r\n  color: #000;\r\n  padding: 8px 16px;\r\n  border: none;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  font-size: 14px;\r\n  transition: background-color 0.3s ease;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  justify-content: center;\r\n  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);\r\n  font-weight: 600;\r\n\r\n  &:hover {\r\n    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);\r\n  }\r\n}\r\n.icon-button-container {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n.debug-section {\r\n  margin-bottom: 1rem;\r\n  p {\r\n    padding: 0 1rem !important;\r\n    font-size: 13px;\r\n  }\r\n}\r\n\r\n.chat-text {\r\n  pre {\r\n    div {\r\n      overflow: auto !important;\r\n    }\r\n    :not(pre) > code[class*=\"language-\"],\r\n    pre[class*=\"language-\"] {\r\n      background: transparent !important;\r\n      text-shadow: none !important;\r\n    }\r\n    .token.operator,\r\n    .token.entity,\r\n    .token.url,\r\n    .language-css .token.string,\r\n    .style .token.string {\r\n      background: transparent !important;\r\n    }\r\n  }\r\n}\r\n\r\n.chat-stop {\r\n  margin-left: 1rem;\r\n  border: none;\r\n  outline: none;\r\n  background-color: #ff4d4f;\r\n  cursor: pointer;\r\n  font-weight: bold;\r\n  color: #fff;\r\n  border-radius: 4px;\r\n  height: 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  padding: 8px 16px;\r\n  font-size: 14px;\r\n}\r\n"],"sourceRoot":""}]);
